
// SECTION 2
const robot_text = "Une Intelligence Artificielle qui analyse et trade sur le marché des crypto-monnaies 24h/24h !";
document.getElementById("robot__text").textContent = robot_text;

const money_text = "Un moyen pour faire fructifier votre argent à hauteur d’environ 10% par mois !";
document.getElementById("money__text").textContent = money_text;

const plus_text = "Un projet aux nombreux objectifs - Cliquez ici pour en savoir plus : <a href='#main-block__section-3'>Nos objetifs</a>";
document.getElementById("plus__text").innerHTML = plus_text;

// AUTO SCROLL
let can_scroll = true;
window.addEventListener('scroll', () => {
	let scroll_y = window.scrollY;

	if (can_scroll) {
			// Scroll down sur première page
			if (scroll_y > 100 && scroll_y < 400) {
				can_scroll = false;
				window.scrollTo(0, 1000);

				setTimeout(() => {
					can_scroll =true;
				}, 300);
			}

			// Scroll up sur deuxième page
			if (scroll_y > 600 && scroll_y < 900) {
				can_scroll = false;
				window.scrollTo(0, 1);

				setTimeout(() => {
					can_scroll =true;
				}, 300);
			};

			// Scroll down sur deuxième page
			if (scroll_y > 1100 && scroll_y < 1400) {
				can_scroll = false;
				window.scrollTo(0, 1988);

				setTimeout(() => {
					can_scroll =true;
				}, 300);
			};

			// Scroll up sur deuxième page
			if (scroll_y > 1600 && scroll_y < 1900) {
				can_scroll = false;
				window.scrollTo(0, 1000);

				setTimeout(() => {
					can_scroll =true;
				}, 300);
			};
	};
});

